body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root, .App {
  height: 100%;
}
.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.App__container {
  display: flex;
  height: 600px;
}
.tree-menu__title:hover  > .tree-menu__submenu--hidden {
  visibility: visible;
}

.tree-menu__submenu--hidden {
  visibility: hidden;
}

.tree-menu__title {
  position: relative;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tree-menu__title p {
  padding-left: 10px;
  margin: 0;
  height: 30px;
  line-height: 30px;
}

.tree-menu__title i {
  padding-right: 10px;
}

.tree-menu__title:hover {
  background-color: #eceff1;
  color: #1a9cb7;
}

.tree-menu__submenu {
  position: absolute;
  left: 100%;
  top: 0;
  box-shadow: 0 0 2px 0 rgba(0,0,0,.25);
  width: 200px;
  background: white;
}
.tree-menu {
  display: block;
  box-shadow: 0 0 2px 0 rgba(0,0,0,.25);
  width: 200px;
  position: relative;
  z-index: 100;
  background: white;
}

ul , li {
  list-style-type: none;
  padding: 0; 
}
